<template>
  <div id="app">
    <div class="signup-modal" v-show="showSignUp">
      <div class="modal-content">
        <div class="close" @click="closeSignUp()">x</div>
        <form name="submit-to-google-sheet">
          <input name="email" type="email" placeholder="Email" required>
          <div>
            <button type="submit">Sign Up</button>
          </div>
        </form>

      </div>
    </div>
    <div class="navbar">
      <div>
        <img class="app-logo" src="./assets/logo.png" />
      </div>
      <div class="nav-item" @click="openSignup()">
        Sign up
      </div>
    </div>
    <div class="banner">
      <div class="logo-container"></div>
      <div class="logo-container-tint"></div>
      <label class="app-announcement">
        Easiest way to find Recreational and Medical Cannabis around you.
      </label>
      <div class="coming-soon-container">
        <img class="coming" src="./assets/googleplay.png" />
        <img class="coming" src="./assets/appstore2.png" />
      </div>
    </div>
    <div class="herbie-phone">
      <img class="phone" src="./assets/herbie-phone.png" />
    </div>
    <footer>
      Made with 
      <span>
        <img style="    max-width: 35px;
    height: auto;" src="./assets/madegif.gif" />
      </span>
      at
      <a style="margin-left: 10px;
    text-decoration: none;
    color: #9871e4;" href="http://f22labs.com/">F22 Labs</a>
    </footer>
  </div>
</template>

<script>
export default {
  name: 'app',
  data () {
    return {
      showSignUp: false
    }
  },
  mounted() {
    const scriptURL = 'https://script.google.com/macros/s/AKfycbyMR4Ij1p4D23yzOH_d8YVq9Za8pxitawui8OnxMaiLWx10Axs/exec'
  const form = document.forms['submit-to-google-sheet']

  form.addEventListener('submit', e => {
    e.preventDefault()
    fetch(scriptURL, { method: 'POST', body: new FormData(form)})
      .then(response => {
        console.log('Success!', response)
        this.showSignUp = false
      })
      .catch(error => console.error('Error!', error.message))
  })
  },
  methods: {
    openSignup() {
      this.showSignUp = true;
    },
    closeSignUp() {
      this.showSignUp = false;
    }
  }
}
</script>

<style lang="scss">
@import url('https://fonts.googleapis.com/css?family=Montserrat:300,400,500,600,700,800');
@import url('https://fonts.googleapis.com/css?family=Lato:400,700,900');

#app {
  text-align: center;
  max-width: 1440px;
  margin: 0 auto;
  font-family: 'Montserrat';
  font-weight: 400;
}

.close {
  font-weight: 600;
  font-family: 'Montserrat';
  position: absolute;
  right: 15px;
  top: 10px;
  font-size: 18px;
  opacity: 0.7;
  cursor: pointer;
}

.signup-modal {
  position: fixed;
  width: 100vw;
  height: 100vh;
  z-index: 100;
  display: flex;
  justify-content: center;
  align-items: center;
}

.modal-content {
  position: relative;
  width: 700px;
  height: 200px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  background-color: #fff;
  box-shadow: 3px 3px 7px 7px rgba(0,0,0,0.1);
}

.modal-content input {
  width: 500px;
  height: 30px;
  padding-left: 10px;
  font-family: 'Montserrat';
  font-size: 18px;
}

.modal-content button {
  cursor: pointer;
  font-size: 16px;
  margin-top: 20px;
  padding: 10px 34px;
  border-radius: 5px;
  font-family: 'Montserrat';
  font-weight: 600;
  background-color: #3fc380;
  color: #fff;
}

footer {
  height: 100px;
  background-color: #3fc380;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #fff;
  font-weight: 600;
  font-size: 14px;
}

.navbar {
  display: flex;
  justify-content: space-between;
  padding: 20px;
  font-family: 'Montserrat';
  font-weight: 600;
  align-items: center;
  color: #3fc380;
}

.herbie-phone {
  display: flex;
  justify-content: center;
}

.coming-soon-container > img {
  max-width: 170px;
  height: auto;
}

.herbie-phone > img.phone {
  width: 100%;
  max-width: 1024px;
  height: auto;
}

.app-logo {
  max-width: 150px;
  height: auto;
}

.nav-item {
  cursor: pointer;
}

.app-announcement {
  font-family: 'Lato';
  font-size: 36px;
  font-weight: 900;
  color: #fff;
  margin-bottom: 25px;
}

.banner {
  text-align: center;
  width: 100%;
  position: relative;
  background: #3fc380;
  display: flex;
  flex-direction: column;
  height: 300px;
  align-items: center;
  justify-content: center;
}

.logo-container {
  background-image: url("./assets/weed_banner.jpg");
  width: 100%;
  height: 300px;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  opacity: 0.1;
  margin: 0 auto;
  position: absolute;
  top: 0;
}

.logo-container-tint {
  width: 100%;
  height: 300px;
  background-color: #3fc380;
  opacity: 0.1;
  margin: 0 auto;
  position: absolute;
  top: 0;
}

h1, h2 {
  font-weight: normal;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #42b983;
}
</style>



